<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process46"></a>

<p><br></p>
<p><span class="rvts17">战斗环境的影响</span></p>
<p><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber136.jpg" display="block" background-size="contain|cover"><span class="rvts18"></span>
<br><span class="rvts21">《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》中的战斗环境分为地表层（</span><span class="rvts20">Surface</span><span class="rvts21">）和云</span><span class="rvts20">(</span><span class="rvts21">雾</span><span class="rvts20">)</span><span class="rvts21">层（</span><span class="rvts20">Cloud</span><span class="rvts21">）。这两种不同的战斗环境会对玩家的战斗带来不同的战斗效果。</span>
<br><span class="rvts21">环境影响会反映在该环境中的角色和敌人所处的优势和劣势，以及遇到怎样的攻击会受到更大的伤害。环境影响对角色施展元素法系技能影响颇大，因此是一个不容忽视的战斗因素。</span>
<br><span class="rvts39"></span>
<br><span class="rvts21">以下是各种环境带来的不同效果和影响：</span>
<br><span class="rvts55">(*</span><span class="rvts56">翻译：</span><span class="rvts55">burning </span><span class="rvts56">燃烧，</span><span class="rvts55">shocked </span><span class="rvts56">震击，</span><span class="rvts55">enweb </span><span class="rvts56">潮寒，</span><span class="rvts55">knocked down </span><span class="rvts56">击倒，</span><span class="rvts55">chilled </span><span class="rvts56">战栗，</span><span class="rvts55">hasted </span><span class="rvts56">急速，</span><span class="rvts55">regeneration </span><span class="rvts56">恢复，</span><span class="rvts55">fortified </span><span class="rvts56">强化，</span><span class="rvts55">stunned </span><span class="rvts56">眩晕，</span><span class="rvts55">petrified </span><span class="rvts56">僵化，</span><span class="rvts55">magic shell </span><span class="rvts56">魔法壳，</span><span class="rvts55">necrofire </span><span class="rvts56">死灵火焰，</span><span class="rvts55">decaying </span><span class="rvts56">腐烂，</span><span class="rvts55">acid </span><span class="rvts56">腐蚀，</span><span class="rvts55">web </span><span class="rvts56">落网，</span><span class="rvts55">blind </span><span class="rvts56">致盲，</span><span class="rvts55">suffocating </span><span class="rvts56">窒息</span><span class="rvts55">*</span><span class="rvts56">）</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">普通地表层面（</span><span class="rvts33">Surface</span><span class="rvts29">）：</span>
<br><span class="rvts57">火层面：</span>
<br><span class="rvts20">* </span><span class="rvts21">造成燃烧效果</span>
<br><span class="rvts20">* </span><span class="rvts21">造成火焰伤害</span>
<br><span class="rvts20">* </span><span class="rvts21">移除潮湿效果</span>
<br><span class="rvts20">* </span><span class="rvts21">使用水系元素或冰系元素可以使火焰效果消失</span>
<br><span class="rvts20">* </span><span class="rvts21">与毒系混合将发生爆炸</span>
<br><span class="rvts20">* </span><span class="rvts21">使用油类或火系法术会使火焰范围变大</span>
<br><span class="rvts20">* </span><span class="rvts21">当火焰熄灭后会留下一片烟雾区域</span>
<br><span class="rvts20">* </span><span class="rvts21">会被魔法护甲格挡</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">水层面：</span>
<br><span class="rvts20">* </span><span class="rvts21">与火元素混合会产生蒸汽</span>
<br><span class="rvts20">* </span><span class="rvts21">与冰元素混合会结冰</span>
<br><span class="rvts20">* </span><span class="rvts21">与电元素混合会产生震击效果</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">电水混合层面：</span>
<br><span class="rvts20">* </span><span class="rvts21">造成气系伤害</span>
<br><span class="rvts20">* </span><span class="rvts21">产生震击效果</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">鲜血层面：</span>
<br><span class="rvts20">* </span><span class="rvts21">与冰元素混合会被冻结</span>
<br><span class="rvts20">* </span><span class="rvts21">与电元素混合会产生震击效果</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">毒层面</span>
<br><span class="rvts20">* </span><span class="rvts21">造成中毒伤害</span>
<br><span class="rvts20">* </span><span class="rvts21">与火系元素混合将发生爆炸</span>
<br><span class="rvts20">* </span><span class="rvts21">造成中毒效果</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">油层面</span>
<br><span class="rvts20">* </span><span class="rvts21">造成减速效果，忽略魔法防御值</span>
<br><span class="rvts20">* </span><span class="rvts21">与火系元素混合会发生爆炸</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">岩浆层面</span>
<br><span class="rvts20">* </span><span class="rvts21">造成大量火焰伤害并瞬间死亡</span>
<br><span class="rvts20">* </span><span class="rvts21">造成燃烧效果</span>
<br><span class="rvts20">* </span><span class="rvts21">很难被移除</span>
<br><span class="rvts20">* </span><span class="rvts21">无法受到祝福，诅咒，冻结或电击</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">源力层面</span>
<br><span class="rvts20">* </span><span class="rvts21">补充源力值</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">潮湿层面</span>
<br><span class="rvts20">* </span><span class="rvts21">有几率进入潮寒状态（无法移动）</span>
<br><span class="rvts20">* </span><span class="rvts21">忽略护甲值</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">冰层面</span>
<br><span class="rvts20">* </span><span class="rvts21">有几率被击倒</span>
<br><span class="rvts20">* </span><span class="rvts21">与火元素混合会变为水层面</span>
<br><span class="rvts43"></span>
<br><span class="rvts57">冰冻血层面</span>
<br><span class="rvts20">* </span><span class="rvts21">有几率被击倒</span>
<br><span class="rvts20">* </span><span class="rvts21">与火元素混合会成为鲜血层面</span>
<br><span class="rvts18"><br></span></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



